// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue'; // Import Swiper styles

import 'swiper/css';
import "swiper/css/pagination";
import "swiper/css/navigation"; // import required modules

import SwiperCore, { Autoplay, Pagination, Navigation } from 'swiper';
SwiperCore.use([Autoplay, Navigation, Pagination]);
import { ref, computed, onMounted, onUnmounted } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import mapComponent from "@/components/mapComponent";
import chatButton from '@/components/chat/chatButton';
import { updateWindowHeight } from "@/utils/tools";
export default {
  name: "businessProfile",
  props: ['info', 'identity', 'canEdit', 'fromMe'],
  components: {
    Swiper,
    SwiperSlide,
    mapComponent,
    chatButton
  },

  setup(props, context) {
    let thumbsSwiper = ref(null);
    const descMoreVisible = ref(true);

    const setThumbsSwiper = swiper => {
      thumbsSwiper.value = swiper;
    };

    const store = useStore();
    const router = useRouter();

    function turnChatPage() {
      router.push({
        path: '/chat/messages'
      });
    }

    const currentUser = computed(() => store.state.currentUser);

    function backToSearchResults() {
      context.emit('back');
    }

    function chatSuccess() {
      turnChatPage();
    }

    onMounted(() => {
      let screenWidth = document.body.clientWidth;
      let screenWidthFloor = Math.floor(screenWidth);

      if (screenWidthFloor <= 768) {
        updateWindowHeight();
      }

      window.onresize = () => {
        if (screenWidthFloor <= 768) {
          updateWindowHeight();
        }
      };
    });
    onUnmounted(() => {
      updateWindowHeight();
      window.onresize = null;
    });
    return {
      descMoreVisible,
      thumbsSwiper,
      setThumbsSwiper,
      currentUser,
      turnChatPage,
      backToSearchResults,
      chatSuccess
    };
  }

};